import { APP_KEY } from '@/constant'

import store from '@/store'
import { throttle } from 'lodash'
import { showError, getData, setData } from '@/utils'

export default function initXmPlayer () {
  const xm = window.xm
  xm.XMPlayerCounter.setup({
    counterType: 1,
    appKey: APP_KEY
  })
  xm.XMPlayer.setup({
    querySoundInfo: 'http://open.ximalaya.com/h5/getDetailedTrackInfo/?id={soundId}',
    breakpoint: true,
    initLast: false,
    debugMode: true,
    supportM3U8: true
  })
  $('body').xmPlayer()
  $.event.add(xm.xmPlayer, 'playstatechange', (e, state) => {
    // READY: 0, //,
    // LOADING: 1, //,1.2.3.seek 
    // PLAYING: 2, //
    // PAUSED: 3, //
    // STOPED: 4, //,
    // FINISHED: 5,//,
    // LOADERROR: 6//
    let startPlaytime = ''
    store.player.setPlayState(state)
    switch (state) {
      case 0:
        store.player.setError(false)
        break
      case 2:
        startPlaytime = e.timeStamp
        setData('startPlaytime', startPlaytime)
        break
      case 3:
        store.history.UphistoryItem(1, e.target.audio.sound.album_id, e.target.audio.sound.id, getData('xm_player_sound_position'), getData('startPlaytime'), Date.parse(new Date()))
        store.history.UphistoryById(e.target.audio.sound.id, e.target.audio.sound.duration, getData('xm_player_sound_position'), getData('startPlaytime'))
        break
      case 4:
        // 
        console.log('')
        store.history.UphistoryItem(1, e.target.audio.sound.album_id, e.target.audio.sound.id, getData('xm_player_sound_position'), getData('startPlaytime'), Date.parse(new Date()))
        store.history.UphistoryById(e.target.audio.sound.id, e.target.audio.sound.duration, getData('xm_player_sound_position'), getData('startPlaytime'))
        break
      case 6:
        store.player.pause()
        store.player.setError(true)
        break
      default:
        break
    }
  })
  $.event.add(xm.xmPlayer, 'soundchange', (e, sound) => {
    store.player.updateTracks(sound)
  })
  $.event.add(xm.xmPlayer, 'timeupdate', throttle((e, position) => {
    store.player.setPosition(position)
  }, 100)
  )
  $.event.add(xm.xmPlayer, 'playModeChange', (e) => {
    // playMode
    // 0
    // 1
    // 2
    const mode = e.target.playMode
    store.player.setPlayMode(mode)
  })

  $.event.add(xm.xmPlayer, 'soundUnusable', function (e, sound) {
    store.player.handleUnusable(sound)
  })

  $.event.add(xm.xmPlayer, 'soundNeedPay', function (e, sound) {
    if (store.player.playState === 5) showError('')
  })

  $.event.add(xm.xmPlayer, 'loaderror', () => {
    store.player.setError(true)
  })
}
