let fence = '$%^&*$'
export default {
  /**
   * 
   * @param key {string}
   * @param value {string|number|object}, numberstring
   * @param expires {number}, expires
   * @returns {boolean}
   */
  set: function (key, value, expires) {
    expires = expires || ''
    try {
      // value
      // parse
      const type = typeof value
      if (value !== null && type === 'object' && type !== 'function') {
        value = JSON.stringify(value) + fence + 'object'
      } else {
        value += fence
      }

      // 
      if (expires) {
        expires = +new Date(+new Date() + expires)
        value += fence + expires
      }
      window.localStorage.setItem(key, value)
      return true
    } catch (e) {
      return false
    }
  },

  /**
   * 
   * @param key
   * @returns {boolean}
   */
  get: function (key) {
    try {
      let data = window.localStorage.getItem(key)
      data = data.split(fence)

      // 
      /* if(data[2]){
        if(data[2] > + new Date()){
          if(data[1] === 'object'){
            return JSON.parse(data[0]);
          }else {
            return data[0];
          }
        }else {
          return false;
        }
      }else{
        // 
        if(data[1] === 'object'){
          return JSON.parse(data[0]);
        }else {
          return data[0];
        }
      } */
      // false
      if (data[2] && data[2] < +new Date()) {
        this.del(key)
        return false
      } else {
        // 
        if (data[1] === 'object') {
          return JSON.parse(data[0])
        } else {
          return data[0]
        }
      }
    } catch (e) {
      return false
    }
  },

  /**
   * 
   * @param key
   * @returns {boolean}
   */
  del: function (key) {
    try {
      window.localStorage.removeItem(key)
      return true
    } catch (e) {
      return false
    }
  },

  /**
   * 
   */
  clear: function () {
    try {
      window.localStorage.clear()
      return true
    } catch (e) {
      return false
    }
  },

  supported: function () {
    let mod = 'supported'
    try {
      window.localStorage.setItem(mod, mod)
      window.localStorage.removeItem(mod)
      return true
    } catch (e) {
      return false
    }
  }
}
