import React, { Component } from 'react'
import { observer, inject } from 'mobx-react'
import TabLink from '@/components/tabLink/tabLink'
// import styled from 'styled-components'
import FlowList from '@/components/flowList/flowList'
import HomeList from './components/homeList'
import CurrentPlay from './components/currenPlay'
import './index.less'

@inject('store')
@observer
export default class Home extends Component {
  render () {
    const { track } = this.props.store.player
    const hasTrack = !!Object.keys(track).length
    return (
      <div className='home-page'>
        <TabLink >
          <li title='' link='/search'>
            <img src={require('../../assets/images/Icons/ic_search.png')} alt='bg' />
          </li>
          <li title='' link='/'key='home'></li>
          <li title=''link='/classify' ></li>
          {/* <li title='' link='/broadcast'></li> */}
          <li title='' link='/user'>
            <img src={require('../../assets/images/Icons/ic_user.png')} alt='bg' />
          </li>
        </TabLink>
        <FlowList>
          <li title='' url='/subscribeAlbum' size='32'>  <img src={require('../../assets/images/Icons/home_ic_collection.png')} alt='bg' /></li>
          <li title='' url='/history' size='32'> <img src={require('../../assets/images/Icons/home_ic_history.png')} alt='bg' /></li>
          <li title='' url='/boughtList' size='32'> <img src={require('../../assets/images/Icons/home_ic_purchase.png')} alt='bg' /></li>
        </FlowList>

        <HomeList {...this.props} />

        {/* logo */}
        <div className='logo'>
          <img src={require('../../assets/images/Images/img_logo.png')} alt='bg' />
        </div>
        { hasTrack && <CurrentPlay /> }
      </div>
    )
  }
}
